import { NativeModules } from 'react-native';

/**
 * 返回当前网络类型
 * @returns
 *  WIFI: 'WIFI',
    MOBILE_2G: 'MOBILE_2G',
    MOBILE_3G: 'MOBILE_3G',
    MOBILE_4G: 'MOBILE_4G',
    MOBILE: 'MOBILE',
    UN_CONNECT: '无网络',
 */
export function getNetworkType() {
    return NativeModules.Kds.invokeSync('system', 'getNetworkType');
}
